import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave } from 'components/common/button';
import { Card, Row, Col, Table, Button } from 'antd';
import { listClassOfficers } from 'dump/data';
import SearchBar from './SearchBar';
import './style.scss'

const table1 = [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="primary">Chọn</Button>
      </Fragment>
    ),
  },

  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Chức danh',
    dataIndex: 'CD',
    key: 'CD',
  },
  {
    title: 'Chức danh kim',
    dataIndex: 'CDK',
    key: 'CDK',
  },

];

const table2 = [


  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Chức danh',
    dataIndex: 'CD',
    key: 'CD',
  },
  {
    title: 'Chức danh kim',
    dataIndex: 'CDK',
    key: 'CDK',
  },
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="danger">Xóa</Button>
      </Fragment>
    ),
  },

];

class ListClassOfficers extends Component {
  state = {};

  render() {

    return (
      <Fragment>
        <Card title="Chọn cán bộ lớp">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table bordered dataSource={listClassOfficers} columns={table1} />
            </Col>
          </Row>
        </Card>
        <Card title="Danh sách cán bộ lớp">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
            <Col span={24}>
              <Table bordered dataSource={listClassOfficers} columns={table2} />
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListClassOfficers />
  </Page>
);



